import "./App.css";

// don't necessarily need to take props
//this function component has an implicit return
const Title = (props) => <h1 className="title">{props.text}</h1>;

//dosen't have to use ES6 can use normal function
function Copy() {
  return (
    <p>
      Lorem ipsum, dolor sit amet consectetur adipisicing elit. Aliquid quis
      perferendis autem fugiat iure, labore veniam modi officiis ad provident
      earum. Natus, laborum autem saepe vitae voluptatem sed deserunt possimus?
    </p>
  );
}

function App() {
  return (
    <div className="box">
      <Title text="Jeff Goldblumm says:" />
      <Copy />
    </div>
  );
}

export default App;
